<script>
import bus from "@/bus.js";
import $ from "jquery";
import {
  Dropdown,
  DropdownItem,
  DropdownMenu,
  Table,
  TableColumn,
} from "element-ui";
import lessons from "./Teachers/lessons";
export default {
  components: {
    InlineActionsTable: () => import("./Teachers/RegularTables/InlineActionsTable"),
    [Dropdown.name]: Dropdown,
    [DropdownItem.name]: DropdownItem,
    [DropdownMenu.name]: DropdownMenu,
    [Table.name]: Table,
    [TableColumn.name]: TableColumn,
    UserBasicInfo: () => import("@/tkViews/components/layout/UserBasicInfo.vue"),
    CreateStudent: () => import("./components/Modal/CreateStudent.vue"),
    TeacherLessonItem: () => import("@/tkViews/components/layout/TeacherLessonItem.vue"),
    SearchBarWithAvatar: () => import("./components/layout/SearchBarWithAvatar.vue"),
  },
  data() {
    return {
      showCreateNewTeacher: false,
      showDelete: false,
      showEditTeacher: false,
      deleteActive: false,
      fullName: "",
      phone: "",
      email: "",
      url: "",
      isInvalid: false,
      showPreview: false,
      previewData: {},
      testStudentData: {},
      lessons,
      editLesson: false,
      showEditLessonDetail: false,
      showAddNewLesson: false,
    };
  },
  methods: {
    showLessonDetail(data) {
      let self = this;
      if (!self.editLesson) {
        self.showEditLessonDetail = true;
        self.testStudentData.name = data.name;
        self.testStudentData.image = "";
        self.testStudentData.email = "test-email@tunkey.app";
      }
    },
    deleteLesson(index) {
      this.lessons.splice(index, 1);
    },
    toggleEditLesson() {
      let self = this;
      self.editLesson = !self.editLesson;
      $("#addNewLesson").slideToggle();
    },
    validationEmail() {
      let self = this;
      if (self.$tools.isNull(self.email)) {
        self.isInvalid = false;
      } else {
        self.isInvalid = !self.$tools.isEmail(self.email);
      }
    },
    preview(data) {
      let self = this;
      self.showPreview = true;
      self.previewData = data;
      self.fullName = self.previewData.name;
      self.email = self.previewData.email;
      self.phone = self.previewData.phone;
      self.url = self.previewData.url;
    },
    onActive() {
      let self = this;
      self.previewData.active = true;
      bus.$emit("onActive", self.previewData);
    },
    onDeleteArchive() {
      let self = this;
      self.deleteActive = false;
      self.showDelete = true;
    },
    onDelete() {
      let self = this;
      self.deleteActive = true;
      self.showDelete = true;
    },
    onArchive() {
      let self = this;
      self.previewData.active = false;
      bus.$emit("onArchive", self.previewData);
    },
    confirmDelete() {
      let self = this;
      bus.$emit("confirmDelete", self.previewData);
      self.showDelete = false;
      self.showPreview = false;
    },
  },
};
</script>
